n  letter-spacing: 2px;\n  line-height: normal;\n}\n\n.text-preset-9 {\n  font-family: var(--barlow-regular);\n  font-size: 0.9375em;\n  font-weight: normal;\n  letter-spacing: 0;\n  line-height: 180%;\n}\n\n/* General Styling */\nbody {\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n  min-height: 100vh;\n\n  background-image: url(\"./assets/home/background-home-mobile.jpg\");\n  background-position: center;\n  background-repeat: no-repeat;\n  background-size: cover;\n}\n\nbody > * {\n  width: 100%;\n  max-width: 1800px;\n  padding: var(--spacing-300);\n}\n\n.number {\n  font-weight: bold;\n  letter-spacing: 2.7px;\n  margin-right: var(--spacing-150);\n}\n\n/* HEADER */\nheader {\n  flex: 0;\n  min-height: 72px;\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n\n  .logo {\n    img {\n      width: 40px;\n      height: auto;\n    }\n  }\n\n  .mobile-menu.hidden {\n    transform: translateX(100%);\n  }\n\n  .mobile-menu {\n    position: fixed;\n    top: 0;\n    right: 0;\n    bottom: 0;\n    min-width: 67.73%;\n    background-color: var(--blue-900-opaque);\n    backdrop-filter: blur(24px) brightness(75%);\n    transform: translateX(0);\n    transition: transform 0.3s linear;\n\n    div {\n      width: 100%;\n      padding: var(--spacing-400) var(--spacing-300);\n      text-align: end;\n    }\n\n    ul {\n      width: 100%;\n      padding: var(--spacing-400) var(--spacing-300);\n      list-style: none;\n      display: flex;\n      flex-direction: column;\n      gap: var(--spacing-400);\n\n      li a {\n        text-decoration: none;\n        color: var(--white);\n        cursor: pointer;\n        padding-bottom: 4px;\n        border-bottom: 3px solid transparent;\n      }\n\n      li:hover {\n        a {\n          border-bottom: 3px solid var(--white);\n        }\n      }\n    }\n  }\n}\n\n.img-button {\n  background-color: transparent;\n  border: none;\n}\n\n/* MAIN */\nmain {\n  flex: 1;\n  display: flex;\n  flex-direction: column;\n\n  section {\n    flex: 1;\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    text-align: center;\n    color: var(--blue-300);\n\n    h1 {\n      color: var(--white);\n    }\n\n    .text-wrapper {\n      display: flex;\n      flex-direction: column;\n      gap: var(--spacing-300);\n    }\n\n    .button-wrapper {\n      flex: 1;\n      display: flex;\n      align-items: center;\n\n      #explore-btn {\n        color: var(--blue-900);\n        background-color: var(--white);\n        width: 9rem;\n        height: 9rem;\n        border: none;\n        border-radius: 999px;\n        box-shadow: 0 0 0 0 transparent;\n      }\n\n      #explore-btn:hover {\n        box-shadow: 0 0 0 88px var(--white-transparent);\n      }\n    }\n  }\n}\n\n@media (max-width: 767px) {\n  .main-menu {\n    display: none;\n  }\n}\n\n@media (min-width: 768px) {\n  body {\n    background-image: url(\"./assets/home/background-home-tablet.jpg\");\n  }\n  /* HEADER */\n  header {\n    min-height: 96px;\n    padding: 0;\n\n    .logo {\n      margin-right: -32px;\n      max-width: 160px;\n      padding-left: var(--spacing-500);\n      flex: 1 0 0;\n\n      img {\n        width: 48px;\n        height: auto;\n      }\n    }\n\n    nav.main-menu {\n      display: flex;\n      align-items: center;\n      justify-content: flex-end;\n      min-width: 488px;\n      padding: 0 var(--spacing-500);\n      flex: 1 0 0;\n      background-color: rgba(255, 255, 255, 0.05);\n      backdrop-filter: blur(24px) brightness(75%);\n\n      ul li:first-child {\n        span {\n          display: none;\n        }\n      }\n    }\n\n    .main-menu ul {\n      list-style: none;\n      display: flex;\n      align-items: center;\n      gap: var(--spacing-600);\n\n      li {\n        display: flex;\n        flex-direction: column;\n        justify-content: center;\n        height: 96px;\n        border-bottom: 3px solid transparent;\n        cursor: pointer;\n      }\n\n      li:hover {\n        border-bottom: 3px solid var(--white);\n      }\n\n      a {\n        text-decoration: none;\n        color: var(--white);\n      }\n    }\n\n    .hamburger {\n      display: none;\n    }\n  }\n\n  /* MAIN */\n  main {\n    padding: var(--spacing-1600) var(--spacing-500);\n  }\n\n  section {\n    gap: var(--spacing-600);\n  }\n\n  .text-wrapper,\n  .button-wrapper {\n    max-width: 540px;\n  }\n\n  .text-preset-1 {\n    font-size: 9em;\n  }\n\n  .text-preset-4 {\n    font-size: 2em;\n  }\n\n  .text-preset-6 {\n    font-size: 1.75em;\n    letter-spacing: 4px;\n  }\n\n  .text-preset-9 {\n    /* TABLET ONLY - look at for desktop */\n    font-size: 1em;\n  }\n\n  main section .button-wrapper #explore-btn {\n    width: 17rem;\n    height: 17rem;\n  }\n}\n\n@media (min-width: 1248px) {\n  body {\n    justify-content: flex-start;\n    background-image: url(\"./assets/home/background-home-desktop.jpg\");\n  }\n\n  /* HEADER */\n  header {\n    padding-top: var(--spacing-500);\n    .logo {\n      display: flex;\n      align-items: center;\n      gap: var(--spacing-800);\n      padding-left: var(--spacing-800);\n      max-width: none;\n      z-index: 2;\n      .line {\n        height: 1px;\n        flex: 1 0 0;\n        opacity: 0.25;\n        background-color: var(--white);\n      }\n    }\n\n    nav.main-menu {\n      min-width: 664px;\n      padding: 0 var(--spacing-800);\n\n      ul li:first-child {\n        span {\n          display: inline;\n        }\n      }\n    }\n  }\n\n  /* MAIN */\n  main {\n    padding: var(--spacing-1600) 0;\n    align-items: center;\n    max-height: 888px;\n    section {\n      max-width: 1110px;\n      width: 100%;\n      flex-direction: row;\n      justify-content: space-between;\n      align-items: flex-end;\n      gap: 30px;\n      text-align: start;\n      flex: 1 0 0;\n\n      .text-wrapper,\n      .button-wrapper {\n        flex: 1 0 0;\n        max-width: 540px;\n        gap: var(--spacing-300);\n        flex-direction: column;\n      }\n\n      .button-wrapper {\n        align-items: flex-end;\n      }\n    }\n  }\n\n  .text-preset-9 {\n    font-size: 1.125em;\n  }\n}\n"],"sourceRoot":""}]);
